import React from "react";

// reactstrap components

// core components
import MyIndexNavbar from "components/Navbars/MyIndexNavbar.js";
import HomePageHeader from "components/Headers/HomePageHeader.js";
import MyFooter from "components/Footers/MyFooter.js";

// index sections
import SectionFeatures from "views/home-sections/SectionFeatures.js";
import SectionTeam from "views/home-sections/SectionTeam.js";
import SectionContact from "views/home-sections/SectionContact.js";

function Home() {
  document.documentElement.classList.remove("nav-open");
  React.useEffect(() => {
    document.body.classList.add("index");
    return function cleanup() {
      document.body.classList.remove("index");
    };
  });
  return (
    <>
      <MyIndexNavbar />
      <HomePageHeader />
      <div className="main">
        <SectionFeatures />
        <SectionTeam />
        <SectionContact />
        <MyFooter />
      </div>
    </>
  );
}

export default Home;
