import React from "react";

// reactstrap components

// core components
import MyIndexNavbar from "components/Navbars/MyIndexNavbar.js";
import AboutPageHeader from "components/Headers/AboutPageHeader.js";
import MyFooter from "components/Footers/MyFooter.js";

// index sections
import SectionStory from "views/about-sections/SectionStory.js";
import SectionProcess from "views/about-sections/SectionProcess.js";
import SectionFutureFeatures from "views/about-sections/SectionFutureFeatures.js";
import SectionThanks from "views/about-sections/SectionThanks.js";

function About() {
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    return function cleanup() {
      document.body.classList.remove("landing-page");
    };
  });
  return (
    <>
      <MyIndexNavbar />
      <AboutPageHeader />
      <div className="main">
        <SectionStory />
        <SectionProcess />
        <SectionFutureFeatures />
        <SectionThanks />
        <MyFooter />
      </div>     
    </>
  );
}

export default About;
